================================ */
/* ===============================
üçã ÂÖ®Â±ÄËÉåÊôØ + App ‰∏ªÂÆπÂô®
================================ */

body {
  margin: 0;
  font-family: "Nunito", "PingFang SC", "Helvetica Neue", sans-serif;
  background: linear-gradient(135deg, #fff8dc, #ffe59a);
  color: #2d3436;
  transition: background 0.5s ease;
  background-attachment: fixed; /* ‚úÖ Èò≤Ê≠¢ÊªöÂä®Êó∂ËÉåÊôØÁßªÂä® */
}

.app {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 30px 20px;
  background: transparent; /* ‚úÖ ËÆ© body Ê∏êÂèòÈÄèÂá∫Êù• */
}

/* ===============================
üçØ ‰∏ªÂÆπÂô®Âç°Áâá
================================ */

/* ===============================
üåû ÂøÉÁÅµËÉΩÈáèÁ´ôÂ∏ÉÂ±Ä‰ºòÂåñ
================================ */
.container {
  width: 100%;
  max-width: 560px; /* ‚úÖ Á®çÂæÆÊõ¥ÂÆΩ‰∏ÄÁÇπ */
  margin: 0 auto;
  padding: 40px 30px; /* ‚úÖ ‰∏ä‰∏ãÁïôÁôΩÊõ¥Â§ö */
  background: rgba(255, 255, 255, 0.95);
  border-radius: 24px;
  box-shadow: 0 8px 30px rgba(255, 200, 0, 0.25);
  text-align: center;
  line-height: 1.8; /* ‚úÖ ÊèêÈ´òÊï¥‰ΩìË°åË∑ù */
}

/* ===============================
üìÖ Ê†áÈ¢òÂå∫‰ºòÂåñ
================================ */
.container h1 {
  margin-bottom: 12px; /* ‚úÖ ‚ÄúÂøÉÁÅµËÉΩÈáèÁ´ô‚Äù‰∏é‰∏ã‰∏ÄË°åÂàÜÈöî */
  font-size: 28px;
}

.container p {
  margin-bottom: 22px; /* ‚úÖ ‚Äú‰ªäÂ§©ÊòØ‚ÄùË°å‰∏ãÊñπÂ¢ûÂä†ÁïôÁôΩ */
}

/* ===============================
üåà Ê†áÁ≠æÂàáÊç¢ÁïôÁôΩË∞ÉÊï¥
================================ */
.tabs {
  margin-top: 16px;
  margin-bottom: 26px; /* ‚úÖ ‰∏é‰∏ãÊñπËØ≠ÂΩïÂÜÖÂÆπÂàÜÈöîÂºÄ */
  display: flex;
  justify-content: space-around;
  gap: 12px;
}


/* ‚ú® ÂΩ©ËõãËÉåÊôØ */
.bg-celebration {
  background: linear-gradient(135deg, #fff86b, #ffb347);
}

/* ===============================
üéÜ È°∂ÈÉ®Ê†áÈ¢òÂå∫
================================ */
.header {
  text-align: center;
  margin-bottom: 25px;
}

.header h1 {
  font-size: 26px;
  font-weight: 700;
  background: linear-gradient(135deg, #ffd43b, #ffa94d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header p {
  margin: 6px 0;
  color: #636e72;
  font-weight: 500;
}

.festival {
  color: #e17055;
  font-weight: 600;
  font-size: 15px;
}

/* ===============================
üåô Ê†áÁ≠æÂàáÊç¢
================================ */
.tabs {
  display: flex;
  justify-content: space-between;
  background: #fff9e6;
  border-radius: 50px;
  padding: 6px;
  margin-bottom: 25px;
  box-shadow: inset 0 2px 5px rgba(255, 200, 0, 0.2);
}

.tabs button {
  flex: 1;
  padding: 10px 15px;
  border: none;
  border-radius: 50px;
  background: transparent;
  color: #b29500;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
}

.tabs button.active {
  background: linear-gradient(135deg, #ffe259, #ffa751);
  color: #2d3436;
  transform: scale(1.05);
}

/* ===============================
ü™Ñ ÂÜÖÂÆπÂå∫
================================ */
.content {
  background: white;
  border-radius: 18px;
  padding: 25px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.05);
  min-height: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.message {
  font-size: 20px;
  line-height: 1.6;
  font-weight: 500;
}

.fortune h2 {
  font-size: 22px;
  margin: 0;
  color: white;
}

.fortune-box {
  padding: 12px 28px;
  border-radius: 50px;
  margin-bottom: 15px;
  box-shadow: 0 6px 15px rgba(255, 200, 0, 0.3);
}

.placeholder {
  color: #b2bec3;
}

/* ===============================
‚ö° ÊåâÈíÆÊ†∑Âºè
================================ */
.actions {
  text-align: center;
  margin-top: 20px;
}

.btn-primary,
.btn-secondary {
  border: none;
  border-radius: 50px;
  padding: 14px 36px;
  font-weight: 600;
  cursor: pointer;
  color: white;
  transition: all 0.3s ease;
}

.btn-primary {
  background: linear-gradient(135deg, #ffe259, #ffa751);
  color: #2d3436;
  box-shadow: 0 6px 15px rgba(255, 200, 0, 0.4);
}

.btn-secondary {
  background: linear-gradient(135deg, #ffd43b, #fab005);
  color: #2d3436;
  box-shadow: 0 6px 15px rgba(255, 200, 0, 0.35);
}

.btn-primary:hover,
.btn-secondary:hover {
  transform: scale(1.05);
}

/* ===============================
üî¢ ÂêçÂ≠óÊµãËØï
================================ */
.name-test {
  margin-top: 25px;
  text-align: center;
}

.name-test input {
  padding: 10px;
  width: 80%;
  border-radius: 10px;
  border: 1px solid #f1c40f;
  outline: none;
  text-align: center;
  font-size: 15px;
  transition: 0.2s;
}

.name-test input:focus {
  box-shadow: 0 0 8px rgba(255, 200, 0, 0.4);
}

/* ===============================
üåø ËÉΩÈáèÊâìÂç°
================================ */
.challenge {
  margin-top: 25px;
  background: linear-gradient(135deg, #fff3b0, #ffe59a);
  border-radius: 16px;
  padding: 18px;
  text-align: center;
  box-shadow: 0 6px 15px rgba(255, 200, 0, 0.25);
}

.challenge button {
  margin-top: 8px;
  padding: 10px 28px;
  border: none;
  border-radius: 50px;
  background: linear-gradient(135deg, #ffe259, #ffa751);
  color: #2d3436;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.challenge button:hover {
  transform: scale(1.05);
}

/* ===============================
üì© Ê≥®ÂÜåÊåâÈíÆ
================================ */
.register {
  text-align: center;
  margin-top: 25px;
}

.register button {
  margin: 5px;
  padding: 12px 25px;
  border-radius: 50px;
  border: none;
  background: linear-gradient(135deg, #ffda79, #ffb347);
  color: #2d3436;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 6px 15px rgba(255, 200, 0, 0.25);
  transition: 0.3s;
}

.register button:hover {
  transform: scale(1.05);
}

/* ===============================
üéÅ ÂΩ©ËõãÊèêÁ§∫
================================ */
.easter-egg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 18px;
  background: linear-gradient(135deg, #ffd43b, #fab005);
  color: #2d3436;
  font-weight: 600;
  text-align: center;
  border-radius: 24px 24px 0 0;
  animation: fadeIn 1.2s ease;
}

/* ===============================
üì± ÂìçÂ∫îÂºè
================================ */
@media (max-width: 600px) {
  .container {
    padding: 20px 15px;
  }

  .header h1 {
    font-size: 22px;
  }

  .message {
    font-size: 18px;
  }

  .btn-primary,
  .btn-secondary {
    width: 100%;
  }
}

/* ===============================
üåà Âä®Áîª
================================ */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


/* üé® Âπ∏ËøêËâ≤ËßÜËßâ‰ºòÂåñ */
.fortune-line {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
  margin-top: 8px;
}

.color-box {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ddd;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}

.color-text {
  font-weight: 600;
  color: #333;
}
